.projects {
  padding: 40px;
  background-color: #f0f0f0;
}

.projects-title {
  font-size: 32px;
  font-weight: bold;
  color: #333;
  margin-bottom: 20px;
  text-align: center;
}

.projects-description {
  font-size: 18px;
  color: #333;
  margin-bottom: 40px;
  text-align: center;
}

.projects-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
}


@media (min-width: 768px) {
  .projects-grid {
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  }

  .projects-grid > * {
    width: 100%;
  }

  .projects-grid > *:nth-child(2n) {
    margin-left: 20px;
  }

  .projects-grid > *:nth-child(2n + 1) {
    margin-right: 20px;
  }
}

