.resume {
  max-width: 800px;
  margin: 0 auto;
  font-family: Arial, sans-serif;
}

h1, h2, h3 {
  margin-bottom: 10px;
}

h1 {
  font-size: 2.5em;
  margin-top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

h2 {
  margin-top: 30px;
}

ul {
  margin-left: 30px;
}

.highlights:hover {
  background-color: #f0f0f0;
  padding: 10px;
  border-radius: 5px;
  transition: background-color 300ms, padding 300ms;
}

.technical-skills:hover {
  background-color: #f0f0f0;
  padding: 10px;
  border-radius: 5px;
  transition: background-color 300ms, padding 300ms;
}

.education:hover {
  background-color: #f0f0f0;
  padding: 10px;
  border-radius: 5px;
  transition: background-color 300ms, padding 300ms;
}

.employment-experience:hover {
  background-color: #f0f0f0;
  padding: 10px;
  border-radius: 5px;
  transition: background-color 300ms, padding 300ms;
}

.experience:hover {
  background-color: #f0f0f0;
  padding: 10px;
  border-radius: 5px;
  transition: background-color 300ms, padding 300ms;
}

.fade-enter {
  opacity: 0;
  transform: translateY(-20px);
}

.fade-enter-active {
  opacity: 1;
  transform: translateY(0);
  transition: opacity 500ms, transform 500ms;
}

.fade-exit {
  opacity: 1;
  transform: translateY(0);
}

.fade-exit-active {
  opacity: 0;
  transform: translateY(-20px);
  transition: opacity 500ms, transform 500ms;
}