/* Home.css */

.home {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  text-align: center;
}

.hero-text {
  margin-bottom: 2rem;
  padding: 0 1rem;
}

.hero-text h1 {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 1rem;
}

.hero-text p {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
}

.hero-image {
  position: relative;
  width: 100%;
  max-width: 400px;
  overflow: hidden;
}

.hero-image img {
  width: 100%;
  height: auto;
}

/* CSSTransition styles */
.fade-appear,
.fade-enter {
  opacity: 0;
  transform: translateY(50px);
}

.fade-appear-active,
.fade-enter-active {
  opacity: 1;
  transform: translateY(0);
  transition: opacity 5000ms, transform 5000ms;
}

/* Media queries for responsiveness */
@media (min-width: 768px) {
  .hero {
    flex-direction: row;
    justify-content: space-between;
  }

  .hero-text {
    text-align: left;
  }

  .hero-text h1 {
    font-size: 3rem;
  }

  .hero-text p {
    font-size: 1.5rem;
  }

  .hero-image {
    max-width: 600px;
  }
}
